/* src/components/ContentList.css */

.content-container {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  padding: 20px;
  justify-content: center;
}

.content-item {
  background-color: #f9f9f9;
  border-radius: 8px;
  padding: 20px;
  width: 200px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s;
}

.content-item:hover {
  transform: scale(1.05);
}

.view-content-button {
  background-color: #28a745;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 4px;
  cursor: pointer;
}

.view-content-button:hover {
  background-color: #218838;
}

.content-image {
  width: 100%;
  height: auto;
  border-radius: 5px;
  margin-bottom: 10px;
}