/* src/index.css */

body {
  font-family: Arial, Helvetica, sans-serif;
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
}

h2 {
  text-align: center;
  color: #333;
}

input {
  display: block;
  margin: 10px auto;
  padding: 10px;
  width: 80%;
  max-width: 300px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

button {
  display: block;
  margin: 10px auto;
  padding: 10px 20px;
  font-size: 1rem;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0056b3;
}

div {
  margin: 20px;
}

.video-container {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

video {
  max-width: 100%;
  height: auto;
  border: 2px solid #ccc;
  border-radius: 10px;
}

.content-item {
  background-color: #ffffff;
  padding: 15px;
  margin: 15px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.content-item h3 {
  margin: 0 0 10px 0;
  color: #333;
}

.content-item button {
  background-color: #28a745;
  margin-top: 10px;
}

.content-item button:hover {
  background-color: #218838;
}
